
'use client';

import React, { createContext, useContext, useState, ReactNode, useCallback, useEffect, useMemo } from 'react';
import { useRouter } from 'next/navigation';
import { signOut, onAuthStateChanged, type Auth, type User, GoogleAuthProvider, signInWithPopup } from 'firebase/auth';
import { initializeApp, getApps, getApp, type FirebaseApp } from 'firebase/app';
import { getAuth } from 'firebase/auth';
import { getFirestore, type Firestore, doc, getDoc, setDoc } from 'firebase/firestore';
import { firebaseConfig } from '@/firebase/config';
import { FirebaseProvider } from '@/firebase/provider';
import { Loader2 } from 'lucide-react';
import { IDataService, useDataService } from '@/lib/data';
import { getMockData } from '@/lib/mock-data';

const MOCK_ADMIN_UID = 'user_admin_1';
const MOCK_VOLUNTEER_ID = 'user_volunteer_1';
const MOCK_CHURCH_ID = 'church_123';

interface AuthContextType {
  isLoggedIn: boolean;
  isAdmin: boolean;
  isUserLoading: boolean;
  churchId: string | null;
  userId: string | null;
  dataService: IDataService | null;
  signInWithGoogle: () => void;
  signInAsAdmin: () => void;
  signInAsVolunteer: () => void;
  logout: () => void;
  toggleAdmin: () => void;
  auth: Auth | null;
  firestore: Firestore | null;
  firebaseApp: FirebaseApp | null;
  firebaseUser: User | null;
  isAdmin: boolean;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export function AuthProvider({ children }: { children: ReactNode }) {
  const [firebaseUser, setFirebaseUser] = useState<User | null>(null);
  const [isUserLoading, setIsUserLoading] = useState(true);
  const [useMock, setUseMock] = useState(false);
  const [isAdminView, setIsAdminView] = useState(false);
  const [isAdmin, setIsAdmin] = useState(false);
  const [churchId, setChurchId] = useState<string | null>(null);
  const router = useRouter();

  const { firebaseApp, auth, firestore } = useMemo(() => {
    const isConfigured = getApps().length > 0;
    const app = isConfigured ? getApp() : initializeApp(firebaseConfig);
    const authInstance = getAuth(app);
    const firestoreInstance = getFirestore(app);

    return { firebaseApp: app, auth: authInstance, firestore: firestoreInstance };
  }, []);

  const dataService = useDataService(firestore, useMock);
  const isLoggedIn = !!firebaseUser || useMock;

  const handleUser = useCallback(async (user: User | null) => {
    setIsUserLoading(true);
    if (user) {
      setFirebaseUser(user);
      setUseMock(false); // Real users never use mock data
      
      const userDocRef = doc(firestore!, 'users', user.uid);
      const userDoc = await getDoc(userDocRef);

      if (userDoc.exists()) {
        const userData = userDoc.data();
        const userIsAdmin = userData.role === 'admin';
        setIsAdmin(userIsAdmin);
        setChurchId(userData.churchId);

        // Respect the last view preference or default to role
        const storedView = sessionStorage.getItem('isAdminView');
        const shouldBeAdminView = storedView !== null ? storedView === 'true' : userIsAdmin;
        setIsAdminView(shouldBeAdminView);

        const currentPath = window.location.pathname;
        if (currentPath === '/' || currentPath === '/role-selection' || currentPath.startsWith('/signup')) {
            router.push(shouldBeAdminView ? '/admin' : '/my-schedule');
        }
      } else {
        // This is a new user, direct to role selection
        router.push('/role-selection');
      }
    } else {
      // User is logged out
      setFirebaseUser(null);
      const mockLogin = sessionStorage.getItem('useMock') === 'true';
      setUseMock(mockLogin);
      if (mockLogin) {
          const mockIsAdmin = sessionStorage.getItem('isAdminView') === 'true';
          setIsAdminView(mockIsAdmin);
          setIsAdmin(mockIsAdmin);
          setChurchId(MOCK_CHURCH_ID);
      } else {
          setIsAdminView(false);
          setIsAdmin(false);
          setChurchId(null);
      }
    }
    setIsUserLoading(false);
  }, [router, firestore]);
  
  useEffect(() => {
    if (!auth) return;
    const unsubscribe = onAuthStateChanged(auth, handleUser);
    return () => unsubscribe();
  }, [auth, handleUser]);
  
  // This effect synchronizes the state from sessionStorage on initial load for mock users
  useEffect(() => {
    if (!isUserLoading && !firebaseUser) {
        const mockLogin = sessionStorage.getItem('useMock') === 'true';
        setUseMock(mockLogin);
        if(mockLogin) {
            const mockIsAdmin = sessionStorage.getItem('isAdminView') === 'true';
            setIsAdminView(mockIsAdmin);
            setIsAdmin(mockIsAdmin);
            setChurchId(MOCK_CHURCH_ID);
        }
    }
  }, [isUserLoading, firebaseUser]);


  const signInWithGoogle = useCallback(async () => {
    if (!auth) return;
    const provider = new GoogleAuthProvider();
    try {
      await signInWithPopup(auth, provider);
      // onAuthStateChanged will handle the rest
    } catch (error: any) {
      if (error.code !== 'auth/popup-closed-by-user' && error.code !== 'auth/cancelled-popup-request') {
        console.error("Google Sign-In Error:", error);
      }
    }
  }, [auth]);

  const setupMockUser = useCallback(async (isAdmin: boolean) => {
    sessionStorage.setItem('useMock', 'true');
    sessionStorage.setItem('isAdminView', String(isAdmin));
    
    // Since this is a mock user, we can directly set up mock data
    if (dataService) {
        await dataService.setupInitialData(MOCK_CHURCH_ID);
    }

    setUseMock(true);
    setIsAdminView(isAdmin);
    setIsAdmin(isAdmin);
    setChurchId(MOCK_CHURCH_ID);
    setFirebaseUser(null); // Ensure no real user is set
    setIsUserLoading(false);
    router.push(isAdmin ? '/admin/setup-wizard' : '/my-schedule');
  }, [router, dataService]);

  const signInAsAdmin = useCallback(() => setupMockUser(true), [setupMockUser]);
  const signInAsVolunteer = useCallback(() => setupMockUser(false), [setupMockUser]);

  const logout = useCallback(async () => {
    if (auth && firebaseUser) {
      await signOut(auth);
    }
    sessionStorage.clear();
    setUseMock(false);
    setIsAdminView(false);
    setIsAdmin(false);
    setChurchId(null);
    setFirebaseUser(null);
    router.push('/');
  }, [router, auth, firebaseUser]);
  
  const toggleAdmin = useCallback(() => {
    if (!isLoggedIn || !isAdmin) return;
    const newIsAdminView = !isAdminView;
    sessionStorage.setItem('isAdminView', String(newIsAdminView));
    setIsAdminView(newIsAdminView);
    router.push(newIsAdminView ? '/admin' : '/my-schedule');
  }, [isLoggedIn, isAdmin, isAdminView, router]);
  
  const value = {
    isLoggedIn,
    isAdmin: isAdminView,
    isUserLoading,
    churchId,
    userId: useMock ? (isAdminView ? MOCK_ADMIN_UID : MOCK_VOLUNTEER_ID) : firebaseUser?.uid || null,
    dataService,
    signInWithGoogle,
    signInAsAdmin,
    signInAsVolunteer,
    logout,
    toggleAdmin,
    auth,
    firestore,
    firebaseApp,
    firebaseUser,
    isAdmin
  };

  return (
    <AuthContext.Provider value={value}>
        <FirebaseProvider 
            firebaseApp={firebaseApp} 
            auth={auth} 
            firestore={firestore}
            firebaseUser={firebaseUser}
            isUserLoading={isUserLoading}
        >
            {isUserLoading ? (
                <div className="flex min-h-screen flex-col items-center justify-center gap-4">
                    <Loader2 className="h-16 w-16 text-primary animate-spin" />
                    <p className="text-muted-foreground">Building your dashboard...</p>
                </div>
            ) : children}
        </FirebaseProvider>
    </AuthContext.Provider>
  );
}

export function useAuth() {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
}
