// This hook simulates a database connection using local state.
export function useMockDatabase(): IDataService {
    const initialData = useMemo(() => getMockData(), []);
    const [church, setChurch] = useState<Church>(initialData.church);
    const [events, setEvents] = useState<Event[]>(initialData.events);
    const [volunteers, setVolunteers] = useState<Volunteer[]>(initialData.volunteers);
    const [universalRoles, setUniversalRoles] = useState<UniversalRole[]>(initialData.universalRoles);
    const [serviceTemplates, setServiceTemplates] = useState<ServiceTemplate[]>(initialData.serviceTemplates);
    const [volunteerAvailability, setVolunteerAvailability] = useState<VolunteerAvailability[]>(initialData.volunteerAvailability);
    const [families, setFamilies] = useState<Family[]>(initialData.families);

    const sleep = (ms: number) => new Promise(resolve => setTimeout(resolve, ms));

    const getChurch = useCallback((_churchId: string, callback: (data: Church | null) => void) => {
        callback(church);
        return () => {};
    }, [church]);

    const updateChurch = useCallback(async (_churchId: string, data: Partial<Church>) => {
        await sleep(500);
        setChurch(prev => ({ ...prev, ...data }));
    }, []);

    const getEvents = useCallback((_churchId: string, callback: (data: Event[]) => void) => {
        callback(events);
        return () => {};
    }, [events]);

    const createEvent = useCallback(async (_churchId:string, newEventData: Omit<Event, 'id' | 'roles' | 'churchId'>, templateId?: string) => {
        await sleep(500);
        const template = serviceTemplates.find(t => t.id === templateId);
        const newRoles: Role[] = template ? template.roles.map(tr => ({
            id: `role_${Date.now()}_${Math.random()}`,
            roleName: tr.name,
            assignedVolunteerId: null,
            assignedVolunteerName: null,
            status: 'Pending'
        })) : [];

        const newEvent: Event = {
            ...newEventData,
            id: `event_${Date.now()}`,
            churchId: MOCK_CHURCH_ID,
            roles: newRoles,
        };
        setEvents(prev => [...prev, newEvent]);
        return newEvent;
    }, [serviceTemplates]);

    const addRoleToEvent = useCallback(async (_churchId: string, eventId: string, newRoleData: Omit<Role, 'id'>) => {
        await sleep(400);
        const newRole: Role = {
            ...newRoleData,
            id: `role_${Date.now()}`,
        };
        setEvents(prev => prev.map(event =>
            event.id === eventId
                ? { ...event, roles: [...event.roles, newRole] }
                : event
        ));
    }, []);
    
    const updateRoleInEvent = useCallback(async (_churchId: string, eventId: string, roleId: string, updates: Partial<Role>) => {
        await sleep(400);
        setEvents(prev => prev.map(event =>
            event.id === eventId
                ? { ...event, roles: event.roles.map(role => role.id === roleId ? { ...role, ...updates } : role) }
                : event
        ));
    }, []);
    
    const deleteRoleFromEvent = useCallback(async (_churchId: string, eventId: string, roleId: string) => {
        await sleep(400);
        setEvents(prev => prev.map(event =>
            event.id === eventId
                ? { ...event, roles: event.roles.filter(role => role.id !== roleId) }
                : event
        ));
    }, []);


    const getVolunteers = useCallback((_churchId: string, callback: (data: Volunteer[]) => void) => {
        callback(volunteers);
        return () => {};
    }, [volunteers]);

    const getVolunteer = useCallback((_churchId: string, volunteerId: string, callback: (data: Volunteer | null) => void) => {
        callback(volunteers.find(v => v.id === volunteerId) || null);
        return () => {};
    }, [volunteers]);
    
    const updateVolunteerPreferences = useCallback(async (_churchId: string, volunteerId: string, preferences: VolunteerPreferences) => {
        await sleep(500);
        setVolunteers(prev => prev.map(v => 
            v.id === volunteerId ? { ...v, preferences } : v
        ));
    }, []);

    const getUniversalRoles = useCallback((_churchId: string, callback: (data: UniversalRole[]) => void) => {
        callback(universalRoles);
        return () => {};
    }, [universalRoles]);

    const addUniversalRole = useCallback(async (_churchId: string, name: string, description?: string) => {
        await sleep(400);
        const newRole: UniversalRole = {
            id: `urole_${Date.now()}`,
            name,
            description,
        };
        setUniversalRoles(prev => [...prev, newRole]);
        return newRole;
    }, []);
    
    const updateUniversalRole = useCallback(async (_churchId: string, id: string, data: Partial<Omit<UniversalRole, 'id'>>) => {
        await sleep(400);
        setUniversalRoles(prev => prev.map(r => r.id === id ? {...r, ...data} : r));
    }, []);

    const deleteUniversalRole = useCallback(async (_churchId: string, id: string) => {
        await sleep(400);
        setUniversalRoles(prev => prev.filter(r => r.id !== id));
        setServiceTemplates(prev => prev.map(st => ({
            ...st,
            roles: st.roles.filter(r => r.roleId !== id)
        })));
    }, []);

    const getServiceTemplates = useCallback((_churchId: string, callback: (data: ServiceTemplate[]) => void) => {
        callback(serviceTemplates);
        return () => {};
    }, [serviceTemplates]);

    const createServiceTemplate = useCallback(async (_churchId: string, name: string, roles: TemplateRole[]) => {
        await sleep(500);
        const newTemplate: ServiceTemplate = {
            id: `template_${Date.now()}`,
            name,
            roles,
        };
        setServiceTemplates(prev => [...prev, newTemplate]);
        return newTemplate;
    }, []);

    const updateServiceTemplate = useCallback(async (_churchId: string, id: string, data: Partial<Omit<ServiceTemplate, 'id'>>) => {
        await sleep(500);
        setServiceTemplates(prev => prev.map(t => t.id === id ? { ...t, ...data } : t));
    }, []);
    
    const deleteServiceTemplate = useCallback(async (_churchId: string, id: string) => {
        await sleep(400);
        setServiceTemplates(prev => prev.filter(t => t.id !== id));
    }, []);

    const getVolunteerAvailability = useCallback((_churchId: string, volunteerId: string, callback: (data: Date[]) => void) => {
        if (volunteerId === 'all') {
             const family = families.find(f => f.members.some(m => m.id === MOCK_VOLUNTEER_ID));
             if (!family) {
                 callback([]);
                 return () => {};
             };
             const memberIds = family.members.map(m => m.id);
             
             const allDates = volunteerAvailability
                .filter(va => memberIds.includes(va.volunteerId))
                .flatMap(va => va.unavailableDates);
            
            const commonUnavailableDates = allDates.filter(date => 
                memberIds.every(memberId => 
                    volunteerAvailability.find(va => va.volunteerId === memberId)?.unavailableDates.some(d => isSameDay(d, date))
                )
            );
            callback(commonUnavailableDates);
        } else {
            const availability = volunteerAvailability.find(va => va.volunteerId === volunteerId);
            callback(availability ? availability.unavailableDates : []);
        }
        return () => {};
    }, [volunteerAvailability, families]);

    const toggleVolunteerAvailability = useCallback(async (_churchId: string, volunteerId: string, date: Date) => {
        await sleep(200);
        const toggle = (prev: VolunteerAvailability[]) => {
            const availabilityIndex = prev.findIndex(va => va.volunteerId === volunteerId);
            if (availabilityIndex > -1) {
                const existing = prev[availabilityIndex];
                const isUnavailable = existing.unavailableDates.some(d => isSameDay(d, date));
                const newDates = isUnavailable
                    ? existing.unavailableDates.filter(d => !isSameDay(d, date))
                    : [...existing.unavailableDates, date];
                const newAvailability = { ...existing, unavailableDates: newDates };
                const newFullList = [...prev];
                newFullList[availabilityIndex] = newAvailability;
                return newFullList;
            } else {
                return [...prev, { volunteerId, unavailableDates: [date] }];
            }
        };
        
        if (volunteerId === 'all') {
            const family = families.find(f => f.members.some(m => m.id === MOCK_VOLUNTEER_ID));
            if (!family) return;
            family.members.forEach(member => {
                 setVolunteerAvailability(prev => toggle(prev));
            });
        } else {
             setVolunteerAvailability(prev => toggle(prev));
        }

    }, [families]);
    
    const getFamilyByVolunteerId = useCallback((_churchId: string, volunteerId: string, callback: (data: Family | null) => void) => {
        const volunteer = volunteers.find(v => v.id === volunteerId);
        if (volunteer && volunteer.preferences.familyManagementEnabled) {
            callback(families.find(f => f.id === volunteer.familyId) || null);
        } else {
            callback(null);
        }
        return () => {};
    }, [volunteers, families]);
    
    const addFamilyMember = useCallback(async (_churchId: string, familyId: string, name: string, email: string) => {
        await sleep(500);
        const newVolunteer: Volunteer = {
            id: `user_volunteer_${Date.now()}`,
            name,
            email,
            phone: '',
            familyId,
            preferences: {
                preferredRoleIds: [],
                servingFrequency: 'as-needed',
                familyServingPreference: 'any',
                familyManagementEnabled: false,
            }
        };
        setVolunteers(prev => [...prev, newVolunteer]);
        
        setFamilies(prev => prev.map(f => {
            if (f.id === familyId) {
                return {
                    ...f,
                    members: [...f.members, { id: newVolunteer.id, name, isPrimary: false }]
                }
            }
            return f;
        }));
    }, []);
    
    const removeFamilyMember = useCallback(async (_churchId: string, familyId: string, volunteerId: string) => {
        await sleep(400);
        setFamilies(prev => prev.map(f => {
             if (f.id === familyId) {
                return {
                    ...f,
                    members: f.members.filter(m => m.id !== volunteerId)
                }
            }
            return f;
        }));
        setVolunteers(prev => prev.filter(v => v.id !== volunteerId));
    }, []);

    return useMemo(() => ({
        getChurch, updateChurch, getEvents, createEvent, addRoleToEvent, updateRoleInEvent, deleteRoleFromEvent,
        getVolunteers, getVolunteer, updateVolunteerPreferences, getUniversalRoles, addUniversalRole,
        updateUniversalRole, deleteUniversalRole, getServiceTemplates, createServiceTemplate,
        updateServiceTemplate, deleteServiceTemplate, getVolunteerAvailability, toggleVolunteerAvailability,
        getFamilyByVolunteerId, addFamilyMember, removeFamilyMember,
        setupInitialData: async () => { console.log("Mock setup complete.")},
    }), [
        getChurch, updateChurch, getEvents, createEvent, addRoleToEvent, updateRoleInEvent, deleteRoleFromEvent,
        getVolunteers, getVolunteer, updateVolunteerPreferences, getUniversalRoles, addUniversalRole,
        updateUniversalRole, deleteUniversalRole, getServiceTemplates, createServiceTemplate,
        updateServiceTemplate, deleteServiceTemplate, getVolunteerAvailability, toggleVolunteerAvailability,
        getFamilyByVolunteerId, addFamilyMember, removeFamilyMember
    ]);
}
