
'use client';

import {
  collection,
  doc,
  getDocs,
  getDoc,
  setDoc,
  addDoc,
  updateDoc,
  deleteDoc,
  writeBatch,
  query,
  where,
  Firestore,
  onSnapshot,
} from 'firebase/firestore';
import type {
  Event,
  Role,
  Volunteer,
  UniversalRole,
  ServiceTemplate,
  TemplateRole,
  VolunteerAvailability,
  VolunteerPreferences,
  Church,
  Family,
  FamilyMember,
} from '@/types';
import { isSameDay } from 'date-fns';
import { useMemo } from 'react';
import { getMockData, useMockDatabase } from './mock-data';

// --- DATA SERVICE INTERFACE ---
// This defines the contract for our data operations. Both the real Firestore service
// and the mock service will implement this interface.

export interface IDataService {
    // Church
    getChurch(churchId: string, callback: (data: Church | null) => void): () => void;
    updateChurch(churchId: string, data: Partial<Church>): Promise<void>;

    // Events
    getEvents(churchId: string, callback: (data: Event[]) => void): () => void;
    createEvent(churchId:string, newEventData: Omit<Event, 'id' | 'roles' | 'churchId'>, templateId?: string): Promise<Event>;
    addRoleToEvent(churchId: string, eventId: string, newRoleData: Omit<Role, 'id'>): Promise<void>;
    updateRoleInEvent(churchId: string, eventId: string, roleId: string, updates: Partial<Role>): Promise<void>;
    deleteRoleFromEvent(churchId: string, eventId: string, roleId: string): Promise<void>;

    // Volunteers
    getVolunteers(churchId: string, callback: (data: Volunteer[]) => void): () => void;
    getVolunteer(churchId: string, volunteerId: string, callback: (data: Volunteer | null) => void): () => void;
    updateVolunteerPreferences(churchId: string, volunteerId: string, preferences: VolunteerPreferences): Promise<void>;
    getVolunteerAvailability(churchId: string, volunteerId: string, callback: (data: Date[]) => void): () => void;
    toggleVolunteerAvailability(churchId: string, volunteerId: string, date: Date): Promise<void>;

    // Universal Roles
    getUniversalRoles(churchId: string, callback: (data: UniversalRole[]) => void): () => void;
    addUniversalRole(churchId: string, name: string, description?: string): Promise<UniversalRole>;
    updateUniversalRole(churchId: string, id: string, data: Partial<Omit<UniversalRole, 'id'>>): Promise<void>;
    deleteUniversalRole(churchId: string, id: string): Promise<void>;

    // Service Templates
    getServiceTemplates(churchId: string, callback: (data: ServiceTemplate[]) => void): () => void;
    createServiceTemplate(churchId: string, name: string, roles: TemplateRole[]): Promise<ServiceTemplate>;
    updateServiceTemplate(churchId: string, id: string, data: Partial<Omit<ServiceTemplate, 'id'>>): Promise<void>;
    deleteServiceTemplate(churchId: string, id: string): Promise<void>;

    // Family
    getFamilyByVolunteerId(churchId: string, volunteerId: string, callback: (data: Family | null) => void): () => void;
    addFamilyMember(churchId: string, familyId: string, name: string, email: string): Promise<void>;
    removeFamilyMember(churchId: string, familyId: string, volunteerId: string): Promise<void>;
    
    // Test data setup
    setupInitialData(churchId: string): Promise<void>;
}


// --- FIRESTORE DATA SERVICE ---

class FirestoreDataService implements IDataService {
  private db: Firestore;

  constructor(firestore: Firestore) {
    this.db = firestore;
  }
  
  // --- Church ---
  getChurch(churchId: string, callback: (data: Church | null) => void) {
    const churchRef = doc(this.db, 'churches', churchId);
    return onSnapshot(churchRef, (docSnap) => {
        callback(docSnap.exists() ? docSnap.data() as Church : null);
    });
  }
  
  async updateChurch(churchId: string, data: Partial<Church>) {
    const churchRef = doc(this.db, 'churches', churchId);
    await updateDoc(churchRef, data);
  }

  // --- Events ---
  getEvents(churchId: string, callback: (data: Event[]) => void) {
    const eventsQuery = query(collection(this.db, 'churches', churchId, 'events'), where("churchId", "==", churchId));
    return onSnapshot(eventsQuery, (snapshot) => {
      const events = snapshot.docs.map(doc => ({
        ...doc.data(),
        id: doc.id,
        eventDate: doc.data().eventDate.toDate(), // Convert timestamp to Date
      } as Event));
      callback(events);
    });
  }

  async createEvent(churchId: string, newEventData: Omit<Event, 'id' | 'roles' | 'churchId'>, templateId?: string) {
     const eventsRef = collection(this.db, 'churches', churchId, 'events');
     let roles: Role[] = [];
     if (templateId) {
        const templateRef = doc(this.db, 'churches', churchId, 'serviceTemplates', templateId);
        const templateSnap = await getDoc(templateRef);
        if (templateSnap.exists()) {
            const template = templateSnap.data() as ServiceTemplate;
            roles = template.roles.map(tr => ({
                id: doc(collection(this.db, 'dummy')).id, // Firestore generates ID on write, so we create a temporary one
                roleName: tr.name,
                assignedVolunteerId: null,
                assignedVolunteerName: null,
                status: 'Pending'
            }));
        }
     }
     const newEvent = { ...newEventData, churchId, roles };
     const docRef = await addDoc(eventsRef, newEvent);
     return { ...newEvent, id: docRef.id } as Event;
  }
  
  async addRoleToEvent(churchId: string, eventId: string, newRoleData: Omit<Role, 'id'>) {
    const eventRef = doc(this.db, 'churches', churchId, 'events', eventId);
    const eventSnap = await getDoc(eventRef);
    if (!eventSnap.exists()) throw new Error("Event not found");

    const event = eventSnap.data() as Event;
    const newRole: Role = { ...newRoleData, id: doc(collection(this.db, 'dummy')).id };
    const updatedRoles = [...event.roles, newRole];
    await updateDoc(eventRef, { roles: updatedRoles });
  }

  async updateRoleInEvent(churchId: string, eventId: string, roleId: string, updates: Partial<Role>) {
    const eventRef = doc(this.db, 'churches', churchId, 'events', eventId);
    const eventSnap = await getDoc(eventRef);
    if (!eventSnap.exists()) throw new Error("Event not found");
    
    const event = eventSnap.data() as Event;
    const updatedRoles = event.roles.map(role => 
      role.id === roleId ? { ...role, ...updates } : role
    );
    await updateDoc(eventRef, { roles: updatedRoles });
  }
  
  async deleteRoleFromEvent(churchId: string, eventId: string, roleId: string) {
    const eventRef = doc(this.db, 'churches', churchId, 'events', eventId);
    const eventSnap = await getDoc(eventRef);
    if (!eventSnap.exists()) throw new Error("Event not found");

    const event = eventSnap.data() as Event;
    const updatedRoles = event.roles.filter(role => role.id !== roleId);
    await updateDoc(eventRef, { roles: updatedRoles });
  }


  // --- Volunteers ---
  getVolunteers(churchId: string, callback: (data: Volunteer[]) => void) {
    const q = query(collection(this.db, 'users'), where('churchId', '==', churchId));
    return onSnapshot(q, (snapshot) => {
      const volunteers = snapshot.docs.map(doc => ({
        ...doc.data(),
        id: doc.id,
      } as Volunteer));
      callback(volunteers);
    });
  }

  getVolunteer(churchId: string, volunteerId: string, callback: (data: Volunteer | null) => void) {
      const volRef = doc(this.db, 'users', volunteerId);
      return onSnapshot(volRef, (docSnap) => {
          if (docSnap.exists() && docSnap.data().churchId === churchId) {
              callback(docSnap.data() as Volunteer);
          } else {
              callback(null);
          }
      });
  }

  async updateVolunteerPreferences(churchId: string, volunteerId: string, preferences: VolunteerPreferences) {
    const volRef = doc(this.db, 'users', volunteerId);
    // Ensure the user belongs to the church before updating
    const volSnap = await getDoc(volRef);
    if (volSnap.exists() && volSnap.data().churchId === churchId) {
        await updateDoc(volRef, { preferences });
    } else {
        throw new Error("Volunteer not found in this church.");
    }
  }
  
  getVolunteerAvailability(churchId: string, volunteerId: string, callback: (data: Date[]) => void) {
    const availabilityRef = doc(this.db, 'churches', churchId, 'volunteerAvailability', volunteerId);
    return onSnapshot(availabilityRef, (docSnap) => {
      if (docSnap.exists()) {
        const data = docSnap.data() as VolunteerAvailability;
        callback(data.unavailableDates.map(ts => ts.toDate()));
      } else {
        callback([]);
      }
    });
  }

  async toggleVolunteerAvailability(churchId: string, volunteerId: string, date: Date) {
    const availabilityRef = doc(this.db, 'churches', churchId, 'volunteerAvailability', volunteerId);
    const docSnap = await getDoc(availabilityRef);
    if (docSnap.exists()) {
      const data = docSnap.data() as { unavailableDates: any[] };
      const isUnavailable = data.unavailableDates.some(ts => isSameDay(ts.toDate(), date));
      const newDates = isUnavailable
        ? data.unavailableDates.filter(ts => !isSameDay(ts.toDate(), date))
        : [...data.unavailableDates, date];
      await updateDoc(availabilityRef, { unavailableDates: newDates });
    } else {
      await setDoc(availabilityRef, { unavailableDates: [date] });
    }
  }


  // --- Universal Roles ---
  getUniversalRoles(churchId: string, callback: (data: UniversalRole[]) => void) {
    const rolesQuery = query(collection(this.db, 'churches', churchId, 'universalRoles'), where("churchId", "==", churchId));
    return onSnapshot(rolesQuery, snapshot => {
      const roles = snapshot.docs.map(doc => ({ ...doc.data(), id: doc.id } as UniversalRole));
      callback(roles);
    });
  }

  async addUniversalRole(churchId: string, name: string, description: string = '') {
    const rolesRef = collection(this.db, 'churches', churchId, 'universalRoles');
    const newRoleData = { name, description, churchId };
    const docRef = await addDoc(rolesRef, newRoleData);
    return { ...newRoleData, id: docRef.id };
  }
  
  async updateUniversalRole(churchId: string, id: string, data: Partial<Omit<UniversalRole, 'id'>>) {
    const roleRef = doc(this.db, 'churches', churchId, 'universalRoles', id);
    await updateDoc(roleRef, data);
  }

  async deleteUniversalRole(churchId: string, id: string) {
    const roleRef = doc(this.db, 'churches', churchId, 'universalRoles', id);
    await deleteDoc(roleRef);
  }
  
  
  // --- Service Templates ---
  getServiceTemplates(churchId: string, callback: (data: ServiceTemplate[]) => void) {
    const templatesQuery = query(collection(this.db, 'churches', churchId, 'serviceTemplates'), where("churchId", "==", churchId));
    return onSnapshot(templatesQuery, snapshot => {
      const templates = snapshot.docs.map(doc => ({ ...doc.data(), id: doc.id } as ServiceTemplate));
      callback(templates);
    });
  }

  async createServiceTemplate(churchId: string, name: string, roles: TemplateRole[]) {
    const templatesRef = collection(this.db, 'churches', churchId, 'serviceTemplates');
    const newTemplateData = { name, roles, churchId };
    const docRef = await addDoc(templatesRef, newTemplateData);
    return { ...newTemplateData, id: docRef.id };
  }
  
  async updateServiceTemplate(churchId: string, id: string, data: Partial<Omit<ServiceTemplate, 'id'>>) {
    const templateRef = doc(this.db, 'churches', churchId, 'serviceTemplates', id);
    await updateDoc(templateRef, data);
  }

  async deleteServiceTemplate(churchId: string, id: string) {
    const templateRef = doc(this.db, 'churches', churchId, 'serviceTemplates', id);
    await deleteDoc(templateRef);
  }

  // --- Family Management ---
  getFamilyByVolunteerId(churchId: string, volunteerId: string, callback: (data: Family | null) => void) {
     const q = query(collection(this.db, 'churches', churchId, 'families'), where('memberIds', 'array-contains', volunteerId));
     return onSnapshot(q, (snapshot) => {
         if (snapshot.empty) {
             callback(null);
         } else {
             const familyDoc = snapshot.docs[0];
             callback({ ...familyDoc.data(), id: familyDoc.id } as Family);
         }
     });
  }

  async addFamilyMember(churchId: string, familyId: string, name: string, email: string) {
      const familyRef = doc(this.db, 'churches', churchId, 'families', familyId);
      const userRef = doc(collection(this.db, 'users'));
      
      const newVolunteer: Omit<Volunteer, 'id'> = {
            name,
            email,
            phone: '',
            familyId,
            churchId,
            role: 'volunteer',
            preferences: {
                preferredRoleIds: [],
                servingFrequency: 'as-needed',
                familyServingPreference: 'any',
                familyManagementEnabled: false,
            }
        };

      const familySnap = await getDoc(familyRef);
      if (!familySnap.exists()) throw new Error("Family not found");
      const family = familySnap.data() as Family;
      
      const newMember: FamilyMember = { id: userRef.id, name, isPrimary: false };
      const updatedMembers = [...family.members, newMember];
      const updatedMemberIds = updatedMembers.map(m => m.id);

      const batch = writeBatch(this.db);
      batch.set(userRef, newVolunteer);
      batch.update(familyRef, { members: updatedMembers, memberIds: updatedMemberIds });
      await batch.commit();
  }

  async removeFamilyMember(churchId: string, familyId: string, volunteerId: string) {
      const familyRef = doc(this.db, 'churches', churchId, 'families', familyId);
      const userRef = doc(this.db, 'users', volunteerId);

      const familySnap = await getDoc(familyRef);
      if (!familySnap.exists()) throw new Error("Family not found");
      const family = familySnap.data() as Family;

      const updatedMembers = family.members.filter(m => m.id !== volunteerId);
      const updatedMemberIds = updatedMembers.map(m => m.id);
      
      const batch = writeBatch(this.db);
      batch.delete(userRef);
      batch.update(familyRef, { members: updatedMembers, memberIds: updatedMemberIds });
      await batch.commit();
  }


  // --- TEST DATA SETUP ---
  async setupInitialData(churchId: string) {
    const mockData = getMockData();
    const batch = writeBatch(this.db);
    
    // Set Church
    const churchRef = doc(this.db, 'churches', churchId);
    batch.set(churchRef, mockData.church);

    // Set Universal Roles
    mockData.universalRoles.forEach(role => {
        const roleRef = doc(this.db, 'churches', churchId, 'universalRoles', role.id);
        batch.set(roleRef, { ...role, churchId });
    });

    // Set Service Templates
    mockData.serviceTemplates.forEach(template => {
        const templateRef = doc(this.db, 'churches', churchId, 'serviceTemplates', template.id);
        batch.set(templateRef, { ...template, churchId });
    });
    
    // Set Events
    mockData.events.forEach(event => {
        // Important: Firestore Timestamps must be used for dates
        const eventDataForFirestore = {
            ...event,
            churchId,
            eventDate: event.eventDate, // Already a Date object, will be converted
        };
        const eventRef = doc(this.db, 'churches', churchId, 'events', event.id);
        batch.set(eventRef, eventDataForFirestore);
    });
    
    // Set Volunteer Availability
     mockData.volunteerAvailability.forEach(avail => {
        const availDataForFirestore = {
            unavailableDates: avail.unavailableDates, // Already Date objects
        };
        const availRef = doc(this.db, 'churches', churchId, 'volunteerAvailability', avail.volunteerId);
        batch.set(availRef, availDataForFirestore);
    });
    
    await batch.commit();
    
    // Users and Families need to be handled separately because their top-level IDs are important
    const usersBatch = writeBatch(this.db);
    mockData.volunteers.forEach(volunteer => {
        const userRef = doc(this.db, 'users', volunteer.id);
        usersBatch.set(userRef, { ...volunteer, churchId, role: volunteer.id.includes('admin') ? 'admin' : 'volunteer' });
    });
    await usersBatch.commit();
    
    const familiesBatch = writeBatch(this.db);
    mockData.families.forEach(family => {
        const familyRef = doc(this.db, 'churches', churchId, 'families', family.id);
        familiesBatch.set(familyRef, { ...family, memberIds: family.members.map(m => m.id) });
    });
    await familiesBatch.commit();
  }
}

// --- HOOK TO GET THE APPROPRIATE DATA SERVICE ---

export function useDataService(firestore: Firestore | null, useMock: boolean): IDataService | null {
  const mockService = useMockDatabase();

  const firestoreService = useMemo(() => {
    if (!firestore) return null;
    return new FirestoreDataService(firestore);
  }, [firestore]);

  if (useMock) {
    return mockService;
  }
  
  return firestoreService;
}

    